.div1,
.wrapper {
  align-self: stretch;
}
.div1 {
  position: relative;
  line-height: 58px;
  font-weight: 500;
}
.wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  font-size: var(--header-font-header1-medium-size);
}
.span {
  color: var(--color-gray);
}
.div2 {
  position: relative;
  line-height: 36px;
  font-weight: 500;
}
.container {
  flex-direction: column;
  padding: 0 var(--padding-29xl) 0 0;
}
.container,
.frame-parent,
.px-h3h2-wrapper {
  align-self: stretch;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.px-h3h2-wrapper {
  border-radius: var(--br-xs);
  background-color: var(--color-aliceblue);
  flex-direction: row;
  padding: var(--padding-5xl) var(--padding-29xl) var(--padding-5xl)
    var(--padding-base);
  font-size: var(--font-inherit);
  color: var(--primary-color-primary-default);
  font-family: var(--font-inherit);
}
.frame-parent {
  flex-direction: column;
  gap: var(--gap-xs);
}
.token-default-color {
  margin: 0;
  padding-left: var(--padding-8xl);
}
.token-default-color-container {
  flex: 1;
  position: relative;
  line-height: 30px;
  font-weight: 500;
}
.div {
  position: relative;
  background-color: var(--bw-white);
  box-shadow: var(--border-inside-bottom);
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 64px 120px 100px;
  box-sizing: border-box;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 32px;
  text-align: left;
  font-size: var(--header-font-header3-medium-size);
  color: var(--text-gray-color-text1-3);
  font-family: var(--header-font-header4-medium);
}
